import Ps from 'perfect-scrollbar'

	export default{
		props:{
			data:String
		},

		ready(){
			var phonePanel = document.getElementById('phonePanel');
			Ps.initialize(phonePanel, {
			  wheelSpeed: 2,
			  wheelPropagation: true,
			  minScrollbarLength: 20
			});

			//var phonePanel = document.getElementById('phonePanel');
			//Ps.initialize(phonePanel);
		}
	}


