import leftbar from '../../services/leftbar/leftbar.js'
import link from '../link.vue';
export default {
	
	data () {
		return {
			leftState:'fadeInLeftbar'
		}
	},
	
	methods:{
		touchend () {
			this.leftState = "fadeOutLeftbar"
		},
		
		touchmove () {
			//console.log()
		}
	},
	
	ready () {
		leftbar.setThis.call(leftbar, this);
	},
	
	initBus (bus) {
      	leftbar.regBus(bus);
    },

    components : {link}
}


