import xender from '../../services/xender.js'

export default {
	data () {
		return {
			imgPng:'',
			h:400,
			flag:true
		}
	},
	
	methods:{
		dxender () {
			xender.onDownLoad();
		}
	},
	
    ready () {
        xender.setThis.call(xender, this);
        xender.containerHeight();
   	},

   initBus (bus) {
      xender.regBus(bus);
   },
}


