import loginSer from '../services/login.js'
import inputform from './input.vue'

export default {
   
  data () {
  	return {
  		login:false,
      username:'admin',
      password:''
  	}
  },

  methods: {
      lg () {
        loginSer.login();
      },
        
  },
  
  ready () {
  	 loginSer.setThis.call(loginSer, this);
  },
  
  initBus (bus) {
      loginSer.regBus(bus);
  },

  components:{inputform}

}


