import Config from '../../services/config.js'

  export default {
    
  	props:{
  		data:Object,
  		click:Object
  	},
  	
  	methods:{
  		dbclick () {
  			console.log("data:" , this)
  		},
  		
  		clickselect (index , e) {
  			e.preventDefault();
  			if(this.data.css[index]){
  				this.data.css.$set(index , '');
  			}else{
  				this.data.css.$set(index , true);
  			}
  			Config.Runtime.eventBus.emit("selectImage");
  		}
  		
  	},
  	
  	ready () {
  		
  	}
  }


