import Config from '../../services/config.js'
import playVideo from '../../services/video/playVideo.js'
export default {
	
	data () {
		return {
			visible:false,
			videoSrc:''
		}
	},
	
	methods:{
		closeVideo () {
			this.visible=false;
			this.videoSrc = '';
		}
	},
	
	ready () {
		playVideo.setThis.call(playVideo, this);
	},
	
	initBus (bus) {
      	playVideo.regBus(bus);
    },
}


