import record from '../../services/record/record.js'

export default {
	data (){
		return {
			h:40,
			recordList:[],
			base64:[]
		}
	},
	
	ready () {
		record.setThis.call(record, this);
        record.containerHeight();
	},
	
	initBus (bus) {
      	record.regBus(bus);
    },
}


